import React, {Fragment, useState, useEffect} from 'react';
import ReactFlow from 'react-flow-renderer';

import {nodes, edges} from '../unodes/zero';

function Unodes({ recipe }) {
console.log(recipe);
    //const [NodalRecipesElements, setElements] = useState(NodalRecipesDefaultElements);

    useEffect(()=> {
        const url = `https://ghoraia.github.io/nodal-recipies-db/${recipe}.json`;
        const  getRecipeFromURL = async () => {
            let recipeData =  await fetch(url);
            let recipeObject = await recipeData.json();
            //setElements(recipeObject.elements);
            console.log(recipeObject.elements);
        };
        getRecipeFromURL();
    }, []);

    return (
        <Fragment>
            <h1> Hello from Nodal Recipies </h1>
            <h2> {recipe} </h2>
            <h6> Code recipies in any addressable space in universe</h6>
            <ReactFlow
                nodes={nodes}
                edges={edges}
                fitView
                attributionPosition="top-right"
                style={{ width: '100%', height: '90vh' }}>
            </ReactFlow>
        </Fragment>
    );
}

export default Unodes;